import React, { Component } from "react";
import { connect } from "react-redux";
import { signUpVerified  } from '../../../actions/initActions'
import { emitEvent } from "../../../actions/socketClient";

class VerifyEmail extends Component {
    constructor(props) { super(props) }
    componentWillMount() {
        const { params, history } = this.props;
        // this.ON_EMIT_SOCKET(93332)
        this.props.signUpVerified(params.id, history, this.ON_EMIT_SOCKET)
    }

    ON_EMIT_SOCKET = (userId) => {
        try {
            emitEvent('updatePlan', { id: userId, plan: "" });
        } catch (error) {
            console.error("Error emitting updatePlan event:", error);
        }
    }

    render() { return <></> } 
}

const mapStateToProps = (state) => {
    return { }
}
const mapDispatchToProps = dispatch => {
    return { signUpVerified: (token, history, socketCallback) => dispatch(signUpVerified(token, history, socketCallback)), }
}
export default connect(mapStateToProps, mapDispatchToProps)(VerifyEmail);